import { useDocumentTitle, useRecommendedProducts, useScrollTop } from 'hooks'
import bannerImg from 'images/banner-girl-1.png'
import React from 'react'
import Banner from 'components2/Banner'
import Display from 'components2/Display'
import Page from 'components2/Page'
import { useTranslation } from 'react-i18next'

const RecommendedProducts = () => {
  const { t } = useTranslation()

  useDocumentTitle('Recommended Products | Salinaka')
  useScrollTop()

  const { recommendedProducts, fetchRecommendedProducts, isLoading, error } =
    useRecommendedProducts()

  return (
    <Page>
      <Banner
        title={<h1>{t('recommendedPage.banner.title')}</h1>}
        img={bannerImg}
      />

      <Display
        error={error}
        isLoading={isLoading}
        fetchProducts={fetchRecommendedProducts}
        products={recommendedProducts}
      />
    </Page>
  )
}

export default RecommendedProducts
