import { AppliedFilters, ProductList } from 'components/product'
import Box from 'components2/Box'
import Page from 'components2/Page'
import ProductsGrid from 'components2/ProductsGrid'
import { useDocumentTitle, useScrollTop } from 'hooks'
import React from 'react'
import { shallowEqual, useSelector } from 'react-redux'
import { selectFilter } from 'selectors/selector'

const Shop = () => {
  useDocumentTitle('Shop | Salinaka')
  useScrollTop()

  const store = useSelector(
    (state) => ({
      filteredProducts: selectFilter(state.products.items, state.filter),
      products: state.products,
      requestStatus: state.app.requestStatus,
      isLoading: state.app.loading
    }),
    shallowEqual
  )

  return (
    <Page>
      <Box as="section" position="relative">
        <AppliedFilters filteredProductsCount={store.filteredProducts.length} />
        <ProductList {...store}>
          <ProductsGrid
            full
            skeletonCount={6}
            products={store.filteredProducts}
          />
        </ProductList>
      </Box>
    </Page>
  )
}

export default Shop
