/* eslint-disable react/forbid-prop-types */
import { Boundary, MessageDisplay } from 'components/common'
import React, { useEffect, useState } from 'react'
import { useDispatch } from 'react-redux'
import { setLoading } from 'redux/actions/miscActions'
import { getProducts } from 'redux/actions/productActions'
import Flexbox from 'components2/Flexbox'

const ProductList = ({
  products,
  filteredProducts,
  isLoading,
  requestStatus = null,
  children
}) => {
  const [isFetching, setFetching] = useState(false)
  const dispatch = useDispatch()

  const fetchProducts = () => {
    setFetching(true)
    dispatch(getProducts(products.lastRefKey))
  }

  useEffect(() => {
    if (products.items.length === 0 || !products.lastRefKey) {
      fetchProducts()
    }

    window.scrollTo(0, 0)
    return () => dispatch(setLoading(false))
  }, [])

  useEffect(() => {
    setFetching(false)
  }, [products.lastRefKey])

  if (filteredProducts.length === 0 && !isLoading) {
    return (
      <MessageDisplay
        message={requestStatus?.message || 'No products found.'}
      />
    )
  }

  if (filteredProducts.length === 0 && requestStatus) {
    return (
      <MessageDisplay
        message={requestStatus?.message || 'Something went wrong :('}
        action={fetchProducts}
        buttonLabel="Try Again"
      />
    )
  }

  return (
    <Boundary>
      {children}
      {/* Show 'Show More' button if products length is less than total products */}
      {products.items.length < products.total && (
        <Flexbox
          // style={{ border: '2px solid yellow' }}
          justifyContent="center"
          alignItems="center"
          padding="l-size"
          // border="2px solid blue"
        >
          <button
            className="button button-small"
            disabled={isFetching}
            onClick={fetchProducts}
            type="button"
          >
            {isFetching ? 'Fetching Items...' : 'Show More Items'}
          </button>
        </Flexbox>
      )}
    </Boundary>
  )
}

export default ProductList
