import { SET_CUSTOMER_TYPE_LOCALY } from 'constants/constants'
import { CustomerType } from 'types'

export type SetCustomerTypeLocalyPayload = CustomerType
export const setCustomerTypeLocaly = (
  payload: SetCustomerTypeLocalyPayload
) => ({
  type: SET_CUSTOMER_TYPE_LOCALY,
  payload
})
