import React from 'react'
import { Modal as ModalComponent } from 'components/common'
import { useModal } from 'hooks'
import { CHECKOUT_STEP_1 } from 'constants/routes'
import { useHistory } from 'react-router-dom'
import { useTranslation } from 'react-i18next'
import Flexbox from 'components2/Flexbox'

type Props = {
  isOpenModal: boolean
  onCloseModal: () => void
}

const Modal = ({ isOpenModal, onCloseModal }: Props) => {
  const history = useHistory()

  const { t } = useTranslation()

  const onSignInClick = () => {
    onCloseModal()
    document.body.classList.remove('basket-open')
    history.push(CHECKOUT_STEP_1)
  }

  return (
    <ModalComponent isOpen={isOpenModal} onRequestClose={onCloseModal}>
      <p className="text-center">{t('common.basket.modal.info')}</p>

      <Flexbox marginTop="1rem" alignItems="center" justifyContent="center">
        <button
          className="button button-border button-border-gray button-small"
          onClick={onCloseModal}
          type="button"
        >
          {t('common.basket.modal.continue')}
        </button>
        &nbsp;
        <button
          className="button button-small"
          onClick={onSignInClick}
          type="button"
        >
          {t('common.basket.modal.signIn')}
        </button>
      </Flexbox>
    </ModalComponent>
  )
}

export default Modal
