import {
  IS_AUTHENTICATING,
  LOADING,
  SET_AUTH_STATUS,
  SET_REQUEST_STATUS
} from 'constants/constants'

export const setLoading = (bool = true) => ({
  type: LOADING,
  payload: bool
})

export type SetAuthenticatingPayload = boolean
export const setAuthenticating = (bool: SetAuthenticatingPayload = true) => ({
  type: IS_AUTHENTICATING,
  payload: bool
})

export const setRequestStatus = (status) => ({
  type: SET_REQUEST_STATUS,
  payload: status
})

export type SetAuthStatusPayload = null | any
export const setAuthStatus = (status: SetAuthStatusPayload = null) => ({
  type: SET_AUTH_STATUS,
  payload: status
})
