import Flexbox from 'components2/Flexbox'
import { useDocumentTitle, useScrollTop } from 'hooks'
import React from 'react'
import { useTranslation } from 'react-i18next'
import { useSelector } from 'react-redux'
import { Total } from 'types'
import BasketItem from 'components/common/Basket/BasketItem'
import Actions from './commons/Actions'
import WithCheckout from './commons/WithCheckout'
import StepTracker from './commons/StepTracker'
import ShippingTotal from './commons/ShippingTotal'
import Checkout from './commons/Checkout'
import Wrapper from './commons/Wrapper'

type Props = {
  total: Total
}

const Step1 = ({ total }: Props) => {
  const { basket } = useSelector((store) => ({
    basket: store.basket
  }))
  const { t } = useTranslation()

  useDocumentTitle('Check Out Step 1 | Salinaka')
  useScrollTop()

  const isBasketEmpty = basket.length === 0

  return (
    <Checkout>
      <StepTracker current={1} />

      <Wrapper>
        <h3 className="text-center">{t('checkoutPage.step1.title')}</h3>
        <span className="d-block text-center">
          {t('checkoutPage.step1.desc')}
        </span>

        <br />
        <div className="checkout-items">
          {isBasketEmpty && (
            <Flexbox alignItems="center" justifyContent="center" flexGrow={1}>
              <h5 className="basket-empty-msg">{t('common.basket.empty')}</h5>
            </Flexbox>
          )}

          {basket.map((product) => (
            <BasketItem key={product.id} product={product} />
          ))}
        </div>

        {!isBasketEmpty && <ShippingTotal total={total} />}
        <Actions step={1} />
      </Wrapper>
    </Checkout>
  )
}

export default WithCheckout(Step1)
