import { useDocumentTitle, useFeaturedProducts, useScrollTop } from 'hooks'
import bannerImg from 'images/banner-guy.png'
import React from 'react'
import Banner from 'components2/Banner'
import Page from 'components2/Page'
import Display from 'components2/Display'
import { useTranslation } from 'react-i18next'

const FeaturedProducts = () => {
  const { t } = useTranslation()

  useDocumentTitle('Featured Products | Salinaka')
  useScrollTop()

  const { featuredProducts, fetchFeaturedProducts, isLoading, error } =
    useFeaturedProducts()

  return (
    <Page>
      <Banner
        title={<h1>{t('featuredPage.banner.title')}</h1>}
        img={bannerImg}
      />

      <Display
        full
        error={error}
        isLoading={isLoading}
        fetchProducts={fetchFeaturedProducts}
        products={featuredProducts}
      />
    </Page>
  )
}

export default FeaturedProducts
