import { number, object, string, array, boolean, TypeOf } from 'yup'

const Image = object().shape({
  id: string().required('Image powinno mie id.'),
  url: string().required('Image powinno mie url.')
})

const product = object().shape({
  name: string()
    .required('Product name is required.')
    .max(60, 'Product name must only be less than 60 characters.'),
  price: number()
    .positive('Price is invalid.')
    .integer('Price should be an integer.')
    .required('Price is required.'),
  description: string().required('Description is required.'),
  sizes: array().of(string()).min(1, 'Please enter a size for this product.'),
  isFeatured: boolean(),
  isRecommended: boolean(),
  gallery: array().of(Image),
  quantity: number().integer()
})

export type Product = TypeOf<typeof product>

export default product
