import React from 'react'
import logo from 'images/logo-full.png'
import { Link } from 'react-router-dom'

type Props = {
  onClickLink: (event: any) => void
}

const Logo = ({ onClickLink }: Props) => (
  <div className="logo">
    <Link onClick={onClickLink} to="/">
      <img alt="Logo" src={logo} />
    </Link>
  </div>
)

export default Logo
