import { ArrowLeftOutlined, LoadingOutlined } from '@ant-design/icons'
import { ImageLoader, MessageDisplay } from 'components/common'
import { SHOP } from 'constants/routes'
import {
  useDocumentTitle,
  useProduct,
  useRecommendedProducts,
  useScrollTop
} from 'hooks'
import React, { useEffect, useState } from 'react'
import { Link, useParams } from 'react-router-dom'
import Display from 'components2/Display'
import Page from 'components2/Page'
import { useTranslation } from 'react-i18next'
import Box from 'components2/Box'
import styled from 'styled-components'
import Flexbox from 'components2/Flexbox'
import Square from 'components2/Square'
import Form from './Form'

const ViewProduct = () => {
  const { t } = useTranslation()

  const { id } = useParams<{ id: string }>()
  const { product, isLoading, error } = useProduct(id)
  useScrollTop()
  useDocumentTitle(`View ${product?.name || 'Item'}`)

  const [selectedImage, setSelectedImage] = useState(
    (product && product.gallery && product.gallery[0].url) || ''
  )

  const {
    recommendedProducts,
    fetchRecommendedProducts,
    isLoading: isLoadingFeatured,
    error: errorFeatured
  } = useRecommendedProducts(6)

  useEffect(() => {
    setSelectedImage(product && product.gallery && product.gallery[0].url)
  }, [product])

  return (
    <Page>
      {isLoading && (
        <div className="loader">
          <h4>Loading Product...</h4>
          <br />
          <LoadingOutlined style={{ fontSize: '3rem' }} />
        </div>
      )}

      {error && <MessageDisplay message={error} />}

      {product && !isLoading && (
        <>
          <Link to={SHOP}>
            <h3 className="button-link d-inline-flex">
              <Box marginRight="0.5rem">
                <ArrowLeftOutlined />
              </Box>
              {t('productPage.goBack')}
            </h3>
          </Link>

          <ProductModal>
            <Flexbox flexDirection="column" width="40%" maxWidth="500px">
              <Square>
                <ImageWrapper>
                  <ImageLoader
                    alt={product.name}
                    className="product-modal-image"
                    src={selectedImage}
                  />
                </ImageWrapper>
              </Square>

              <br />
              <div className="divider" />
              <br />

              {product.gallery.length !== 0 && (
                <Gallery>
                  {product.gallery.map((image) => (
                    <GalleryImageWrapper
                      key={image.id}
                      onClick={() => setSelectedImage(image.url)}
                      role="presentation"
                    >
                      <GalleryImage src={image.url} />
                    </GalleryImageWrapper>
                  ))}
                </Gallery>
              )}
            </Flexbox>

            <Details>
              <br />
              <span className="text-subtle">{product.brand}</span>
              <h1 className="margin-top-0">{product.name}</h1>
              <span>{product.description}</span>
              <br />
              <br />
              <div className="divider" />

              <Form product={product} />
            </Details>
          </ProductModal>

          <Display
            marginTop="10rem"
            title={t('productPage.recommended.title')}
            error={errorFeatured}
            isLoading={isLoadingFeatured}
            fetchProducts={fetchRecommendedProducts}
            products={recommendedProducts}
          />
        </>
      )}
    </Page>
  )
}

const ProductModal = styled.div`
  padding: 30px;
  width: 100%;
  display: flex;
  background: #fff;
  border: 1px solid #e1e1e1;

  /* @include tablet {
    flex-direction: column;
    margin: 0;
  } */

  & * {
    /* border: 1px solid red; */
  }
`

const Gallery = styled.div`
  width: 100%;
  background: #fff;
  padding: 5px;
  position: relative;
  display: grid;
  grid-template-columns: repeat(auto-fit, 60px);
  grid-template-rows: repeat(auto-fit, 60px);
  grid-gap: 5px;

  /* @include tablet {
    width: 100%;
    height: auto;
    order: 2;
    overflow-x: scroll;
    overflow-y: unset;
    flex-direction: row;
  } */

  /* &::-webkit-scrollbar {
    width: 10px;
  }

  &::-webkit-scrollbar-thumb {
    background-color: #1a1a1a;
    outline: 1px solid #f8f8f8;
    // border-radius: .6rem;
  }

  &::-webkit-scrollbar-track {
    -webkit-box-shadow: inset 0 0 4px rgba(0, 0, 0, 0.2);
  } */
`

const GalleryImageWrapper = styled.div`
  min-width: 100%;
  max-width: 100%;
  min-height: 60px;
  max-height: 60px;
  border: 1px solid #e1e1e1;
  margin-right: 5px;
  position: relative;

  &:hover {
    cursor: pointer;
    background: #fff;
  }

  &:last-child {
    margin-right: 0;
  }

  /* @include tablet {
    order: 1;
    width: 100px;
  } */
`

const GalleryImage = styled(ImageLoader)`
  width: 100%;
  height: 100%;
  object-fit: contain;
`

const Details = styled.div`
  margin-left: 3rem;
  flex-grow: 1;

  /* @include tablet {
    width: 100%;
    padding: ${(props) => props.theme.space['m-size']};
    padding-bottom: ${(props) => props.theme.space['l-size']};
    order: 3;
  } */
`

const ImageWrapper = styled.div`
  width: 100%;
  height: 100%;
  flex-grow: 1;
  position: relative;

  /* background: #f8f8f8; */

  /* @include mobile {
    width: 100%;
    height: 20rem;
    margin: auto;
  } */

  input[type='color'] {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    mix-blend-mode: hue;
  }
`

export default ViewProduct
