import { SET_PREFERRED_DELIVERY } from 'constants/constants'
import { DeliveryType } from 'types'

const initState = null

type Props = {
  type: typeof SET_PREFERRED_DELIVERY
  payload: DeliveryType
}

export default (state = initState, action: Props) => {
  switch (action.type) {
    case SET_PREFERRED_DELIVERY:
      return action.payload
    default:
      return state
  }
}
