import { CHECKOUT_STEP_1, SIGNIN } from 'constants/routes'
import { calculateTotal } from 'helpers/utils'
import React from 'react'
import { useSelector } from 'react-redux'
import { Redirect, useLocation, withRouter } from 'react-router-dom'
import { Auth, BasketProduct, Total, DeliveryType } from 'types'
import { DELIVERY_METHODS } from '../Step3/OrderDetails'

const WithCheckout = (Component: JSX.Element) =>
  withRouter((props) => {
    type State = {
      auth: Auth
      basket: BasketProduct[]
      preferredDelivery: DeliveryType | null
    }

    const path = useLocation()

    const { auth, basket, preferredDelivery } = useSelector<any, State>(
      (store) => ({
        auth: store.auth,
        basket: store.basket,
        preferredDelivery: store.NEWpreferredDelivery
      })
    )

    const isAuth = !!auth.id && !!auth.role
    if (!isAuth) {
      return <Redirect to={SIGNIN} />
    }

    const isFirstPageOfCheckout = path.pathname === CHECKOUT_STEP_1
    if (basket.length === 0 && !isFirstPageOfCheckout) {
      return <Redirect to={CHECKOUT_STEP_1} />
    }

    const products = calculateTotal(
      basket.map((product) => product.price * product.quantity)
    )

    const delivery = preferredDelivery ? DELIVERY_METHODS[preferredDelivery] : 0
    const total: Total = {
      products,
      delivery,
      sum: products + delivery
    }

    return <Component {...props} auth={auth} total={total} />
  })

export default WithCheckout
