import { CHECKOUT_RESULT } from 'constants/routes'
import { useDispatch, useSelector } from 'react-redux'
import { useHistory } from 'react-router-dom'
import { addOrderRemotely } from 'redux/actions/checkoutActions'
import { setPreferredPayment } from 'redux/actions/new/prefferedPaymentActions'
import { Auth, BasketProduct, PaymentType, Total } from 'types'

import { mixed, object } from 'yup'

export type FormValues = {
  paymentType: PaymentType
}

export const formSchema = object().shape({
  paymentType: mixed().oneOf(['TRANSFER'], 'Wybierz jedn, aby przej dalej.')
})
const useFormSubmit = (total: Total, basket: BasketProduct[], auth: Auth) => {
  const history = useHistory()
  const { paymentType, deliveryType, contactDetails, shipping } = useSelector(
    (store) => ({
      paymentType: store.NEWpreferredPayment,
      deliveryType: store.NEWpreferredDelivery,
      contactDetails: store.NEWcontactDetails,
      shipping: store.NEWshipping
    })
  )
  const dispatch = useDispatch()

  const onSubmit = (form: FormValues) => {
    dispatch(setPreferredPayment(form.paymentType))

    dispatch(
      addOrderRemotely({
        total,
        basket,
        auth,
        paymentType: form.paymentType,
        deliveryType,
        contactDetails,
        shipping
      })
    )

    history.push(CHECKOUT_RESULT)
  }

  const getInitialValues = (): FormValues => ({
    paymentType: paymentType || 'TRANSFER'
  })

  return {
    onSubmit,
    getInitialValues
  }
}

export default useFormSubmit
