import { SET_ORDERS_ITEMS, SET_SHIPPING_LOCALY } from 'constants/constants'
import { SetShippingLocalyPayload } from 'redux/actions/new/shippingActions'
import { Shipping } from 'types'

const initState = null

type Action = {
  type: typeof SET_SHIPPING_LOCALY
  payload: SetShippingLocalyPayload
}

export default (state: Shipping | null = initState, action: Action) => {
  switch (action.type) {
    case SET_SHIPPING_LOCALY:
      return action.payload

    default:
      return state
  }
}
