import Button from 'components2/Button'
import Flexbox from 'components2/Flexbox'
import { SHOP } from 'constants/routes'
import { calculateTotal, displayMoney } from 'helpers/utils'
import React from 'react'
import { useTranslation } from 'react-i18next'
import { useHistory, useLocation } from 'react-router-dom'
import { BasketProduct } from 'types'
import useBasketClickToggle from './useBasketClickToggle'

type Props = {
  basket: BasketProduct[]
  onCheckOut: () => void
}

const Checkout = ({ basket, onCheckOut }: Props) => {
  const { t } = useTranslation()
  const { pathname } = useLocation()
  const history = useHistory()

  const onClickToggle = useBasketClickToggle()

  return (
    <Flexbox
      background="white"
      padding="m-size"
      alignItems="center"
      // justifyContent="space-between"
      width="100%"
    >
      <div>
        <p className="basket-total-title">{t('common.basket.total')}</p>
        <h2 className="basket-total-amount">
          {displayMoney(
            calculateTotal(
              basket.map((product) => product.price * product.quantity)
            )
          )}
        </h2>
      </div>

      <Button
        variant="secondary"
        marginLeft="auto"
        onClick={() => {
          onClickToggle()
          history.push(SHOP)
        }}
      >
        {t('common.basket.backToShop')}
      </Button>
      <Button
        marginLeft="10px"
        onClick={onCheckOut}
        disabled={basket.length === 0 || pathname === '/checkout'}
      >
        {t('common.basket.checkout')}
      </Button>
    </Flexbox>
  )
}

export default Checkout
