import {
  ADD_QTY_ITEM,
  ADD_TO_BASKET,
  CLEAR_BASKET,
  MINUS_QTY_ITEM,
  REMOVE_FROM_BASKET,
  SET_BASKET_ITEMS
} from 'constants/constants'
import { BasketProduct } from 'types'

export const setBasketItems = (items: BasketProduct[] = []) => ({
  type: SET_BASKET_ITEMS,
  payload: items
})

export const addToBasket = (basketProduct: BasketProduct) => ({
  type: ADD_TO_BASKET,
  payload: basketProduct
})

export const removeFromBasket = (basketProduct: BasketProduct) => ({
  type: REMOVE_FROM_BASKET,
  payload: basketProduct
})

export const clearBasket = () => ({
  type: CLEAR_BASKET
})

export const addQtyItem = (basketProduct: BasketProduct) => ({
  type: ADD_QTY_ITEM,
  payload: basketProduct
})

export const minusQtyItem = (basketProduct: BasketProduct) => ({
  type: MINUS_QTY_ITEM,
  payload: basketProduct
})
