import { SET_CONTACT_DETAILS_LOCALY } from 'constants/constants'
import { SetContactDetailsLocalyPayload } from 'redux/actions/new/contactDetailsActions'

type Props = {
  type: typeof SET_CONTACT_DETAILS_LOCALY
  payload: SetContactDetailsLocalyPayload
}

const contactDetails = (state = {}, action: Props) => {
  switch (action.type) {
    case SET_CONTACT_DETAILS_LOCALY:
      return action.payload

    default:
      return state
  }
}

export default contactDetails
