import Flexbox from 'components2/Flexbox'
import { PaymentType } from 'types'
import React from 'react'
import { Field, FieldProps } from 'formik'
import { useTranslation } from 'react-i18next'
import CheckboxField from 'components2/CheckboxField'
import PAYMENT_METHODS from 'constants/paymentMethods'
import ExpandableBox from 'components2/ExpandableBox'
import { FormValues } from './form'

const OrderDetails = () => {
  const { t } = useTranslation()

  return (
    <div className="checkout-shipping-wrapper">
      <div className="checkout-shipping-form">
        <Flexbox
          margin="0 1.2rem"
          flexDirection="column"
          className="checkout-fieldset"
        >
          {PAYMENT_METHODS.map((method, index) => (
            <Field name="paymentType" key={method}>
              {(props: FieldProps<PaymentType, FormValues>) => (
                <CheckboxField
                  {...props}
                  id={method}
                  isFirst={index === 0}
                  title={t(
                    `checkoutPage.step4.form.paymentType.${method}.title`
                  )}
                  subtitle={t(
                    `checkoutPage.step4.form.paymentType.${method}.subtitle`
                  )}
                  fieldLabel={t('checkoutPage.step4.form.paymentType.label')}
                />
              )}
            </Field>
          ))}

          {/* <ExpandableBox /> */}
        </Flexbox>
      </div>
    </div>
  )
}

export default OrderDetails
