import {
  ON_AUTHSTATE_FAIL,
  ON_AUTHSTATE_SUCCESS,
  RESET_PASSWORD,
  SET_AUTH_PERSISTENCE,
  SIGNIN,
  SIGNIN_WITH_FACEBOOK,
  SIGNIN_WITH_GITHUB,
  SIGNIN_WITH_GOOGLE,
  SIGNOUT,
  SIGNUP
} from 'constants/constants'
import { SIGNIN as ROUTE_SIGNIN } from 'constants/routes'
import defaultAvatar from 'images/defaultAvatar.jpg'
import defaultBanner from 'images/defaultBanner.jpg'
import { call, put } from 'redux-saga/effects'
import {
  SignInPayload,
  signInSuccess,
  signOutSuccess,
  SignUpPayload
} from 'redux/actions/authActions'
import { clearBasket } from 'redux/actions/basketActions'
import { setContactDetailsLocaly } from 'redux/actions/new/contactDetailsActions'
import { resetFilter } from 'redux/actions/filterActions'
import { setAuthenticating, setAuthStatus } from 'redux/actions/miscActions'
import { clearProfile, setProfile } from 'redux/actions/profileActions'
import { history } from 'routers/AppRouter'
import firebase from 'services/firebase'
import { User } from 'types'
import { setOrdersItems } from 'redux/actions/ordersActions'

function* handleError(e) {
  const obj = { success: false, type: 'auth', isError: true }
  yield put(setAuthenticating(false))

  switch (e.code) {
    case 'auth/network-request-failed':
      yield put(
        setAuthStatus({
          ...obj,
          message: 'Problem z sieci. Sprbuj ponownie'
        })
      )
      break
    case 'auth/email-already-in-use':
      yield put(
        setAuthStatus({
          ...obj,
          message: 'Email jest ju zarejestrowany. Przejd do strony logowania.'
        })
      )
      break
    case 'auth/wrong-password':
      yield put(
        setAuthStatus({ ...obj, message: 'Incorrect email or password' })
      )
      break
    case 'auth/user-not-found':
      yield put(
        setAuthStatus({ ...obj, message: 'Incorrect email or password' })
      )
      break
    case 'auth/reset-password-error':
      yield put(
        setAuthStatus({
          ...obj,
          message:
            'Failed to send password reset email. Did you type your email correctly?'
        })
      )
      break
    default:
      yield put(setAuthStatus({ ...obj, message: e.message }))
      break
  }
}

function* initRequest() {
  yield put(setAuthenticating())
  yield put(setAuthStatus())
}

type Props =
  | {
      type: typeof SIGNUP
      payload: SignUpPayload
    }
  | {
      type: typeof SIGNIN
      payload: SignInPayload
    }

function* authSaga({ type, payload }: Props) {
  switch (type) {
    case SIGNIN:
      try {
        yield initRequest()
        yield call(firebase.signIn, payload.email, payload.password)
      } catch (e) {
        yield handleError(e)
      }
      break

    case SIGNUP:
      try {
        yield initRequest()

        const ref = yield call(
          firebase.createAccount,
          payload.email,
          payload.password
        )

        const user: User = {
          user: {
            email: payload.email
          },
          role: 'USER',
          avatar: defaultAvatar,
          banner: defaultBanner,
          dateJoined: ref.user.metadata.creationTime || new Date().getTime()
        }

        yield call(firebase.addUser, ref.user.uid, user)
        yield put(setProfile(user))

        yield put(setAuthenticating(false))
      } catch (e) {
        yield handleError(e)
      }
      break

    case SIGNOUT: {
      try {
        yield initRequest()
        yield call(firebase.signOut)
        yield put(clearBasket())
        yield put(clearProfile())
        yield put(resetFilter())
        yield put(signOutSuccess())
        yield put(setAuthenticating(false))
        yield call(history.push, ROUTE_SIGNIN)
      } catch (e) {
        console.log(e)
      }
      break
    }
    case RESET_PASSWORD: {
      try {
        yield initRequest()
        yield call(firebase.passwordReset, payload)
        yield put(
          setAuthStatus({
            success: true,
            type: 'reset',
            message:
              'Password reset email has been sent to your provided email.'
          })
        )
        yield put(setAuthenticating(false))
      } catch (e) {
        handleError({ code: 'auth/reset-password-error' })
      }
      break
    }
    case ON_AUTHSTATE_SUCCESS: {
      const snapshot = yield call(firebase.getUser, payload.uid)

      if (snapshot.data()) {
        // if user exists in database
        const user = snapshot.data()

        yield put(setProfile(user))
        yield put(setContactDetailsLocaly(user.user))

        const userOrders = yield call(firebase.getUserOrders, payload.uid)
        yield put(setOrdersItems(userOrders))

        // yield put(setContactDetailsLocaly(user.contactDetails))

        // yield put(setBasketItems(user.basket))
        yield put(
          signInSuccess({
            id: payload.uid,
            role: user.role,
            provider: payload.providerData[0].providerId
          })
        )
      }

      yield put(
        setAuthStatus({
          success: true,
          type: 'auth',
          isError: false,
          message: 'Successfully signed in. Redirecting...'
        })
      )
      yield put(setAuthenticating(false))
      break
    }
    case ON_AUTHSTATE_FAIL: {
      yield put(clearProfile())
      yield put(signOutSuccess())
      break
    }
    case SET_AUTH_PERSISTENCE: {
      try {
        yield call(firebase.setAuthPersistence)
      } catch (e) {
        console.log(e)
      }
      break
    }
    default: {
      throw new Error('Unexpected Action Type.')
    }
  }
}

export default authSaga
