/* eslint-disable implicit-arrow-linebreak */
/* eslint-disable no-nested-ternary */
/* eslint-disable no-confusing-arrow */
import React from 'react'
import { useTranslation } from 'react-i18next'

type Props = {
  current: 1 | 2 | 3 | 4
}

const StepTracker = ({ current }: Props) => {
  const { t } = useTranslation()

  const getClassName = (step: number) =>
    current === step ? 'is-active-step' : step < current ? 'is-done-step' : ''

  return (
    <div className="checkout-header">
      <ul className="checkout-header-menu">
        {[1, 2, 3, 4].map((step) => (
          <li className={`checkout-header-list ${getClassName(step)}`}>
            <div className="checkout-header-item">
              <div className="checkout-header-icon">
                <h4 className="checkout-header-step">{step}</h4>
              </div>

              <h6 className="checkout-header-subtitle">
                {t(`checkoutPage.steps.${step}`)}
              </h6>
            </div>
          </li>
        ))}
      </ul>
    </div>
  )
}

export default StepTracker
