import { CHECKOUT_STEP_4, CHECKOUT_STEP_1 } from 'constants/routes'
import { Form, Formik } from 'formik'
import { useDocumentTitle, useScrollTop } from 'hooks'
import React from 'react'
import { Redirect } from 'react-router-dom'
import { Auth, BasketProduct, Payment, Total } from 'types'
import WithCheckout from '../commons/WithCheckout'
import StepTracker from '../commons/StepTracker'
import useFormSubmit, { formSchema } from './form'
import Actions from '../commons/Actions'
import ShippingTotal from '../commons/ShippingTotal'
import Checkout from '../commons/Checkout'
import Wrapper from '../commons/Wrapper'
import { useTranslation } from 'react-i18next'
import OrderDetails from './OrderDetails'
import { useSelector } from 'react-redux'

type Props = {
  auth: Auth
  total: Total
}

const Payment = ({ auth, total }: Props) => {
  const { deliveryType, basket } = useSelector((store) => ({
    deliveryType: store.NEWpreferredDelivery,
    basket: store.basket
  }))
  const { t } = useTranslation()
  const { onSubmit, getInitialValues } = useFormSubmit(total, basket, auth)

  useDocumentTitle('Check Out Final Step | Salinaka')
  useScrollTop()

  // if (!deliveryType) {
  //   return <Redirect to={CHECKOUT_STEP_4} />
  // }

  return (
    <Checkout>
      <StepTracker current={4} />

      <Wrapper>
        <h3 className="text-center">{t('checkoutPage.step4.title')}</h3>

        <Formik
          initialValues={getInitialValues()}
          validateOnChange
          validationSchema={formSchema}
          onSubmit={onSubmit}
        >
          {({ handleSubmit }) => (
            <Form onSubmit={handleSubmit}>
              <OrderDetails />
              <ShippingTotal total={total} />
              <Actions step={4} />
            </Form>
          )}
        </Formik>
      </Wrapper>
    </Checkout>
  )
}

export default WithCheckout(Payment)
