/* eslint-disable no-undef */
import { CHECKOUT_STEP_4, CHECKOUT_STEP_3A } from 'constants/routes'
import { useDispatch, useSelector } from 'react-redux'
import { useHistory } from 'react-router-dom'
import { setPreferredDelivery } from 'redux/actions/new/preferredDeliveryActions'
import { DeliveryType } from 'types'
import { object, mixed } from 'yup'

export const formSchema = object().shape({
  deliveryType: mixed().oneOf(
    ['FREE_PARCEL', 'PICK_UP'],
    'Wybierz jedn, aby przej dalej.'
  )
})

export type FormValues = {
  deliveryType: DeliveryType | null
}

const useFormSubmit = () => {
  const { preferredDelivery } = useSelector((store) => ({
    preferredDelivery: store.NEWpreferredDelivery
  }))

  const dispatch = useDispatch()
  const history = useHistory()

  const onSubmitForm = (form: FormValues) => {
    if (form.deliveryType) {
      dispatch(setPreferredDelivery(form.deliveryType))
    }

    history.push(
      form.deliveryType === 'FREE_PARCEL' ? CHECKOUT_STEP_3A : CHECKOUT_STEP_4
    )
  }

  const getInitialValues = (): FormValues => ({
    deliveryType: preferredDelivery || null
  })

  return {
    onSubmitForm,
    getInitialValues
  }
}

export default useFormSubmit
