/* eslint-disable no-use-before-define */
/* eslint-disable jsx-a11y/label-has-associated-control */
import { CustomInput } from 'components/formik'
import { Field, FieldProps } from 'formik'
import React from 'react'
import { useTranslation } from 'react-i18next'
import { City, DeliveryType, HourseNr, PostCode, Price, Street } from 'types'
import { FormValues } from './form'

const OrderDetails = () => {
  const { t } = useTranslation()

  return (
    <div className="checkout-shipping-wrapper">
      <div className="checkout-shipping-form">
        <div className="checkout-fieldset">
          <div className="d-block checkout-field">
            <Field name="street">
              {(props: FieldProps<Street, FormValues>) => (
                <CustomInput
                  {...props}
                  label={getRequiredOrNot(
                    t('checkoutPage.step3a.form.street.label'),
                    true
                  )}
                  placeholder={t('checkoutPage.step3a.form.street.placeholder')}
                />
              )}
            </Field>
          </div>
          <div className="d-block checkout-field">
            <Field name="houseNr">
              {(props: FieldProps<HourseNr, FormValues>) => (
                <CustomInput
                  {...props}
                  label={getRequiredOrNot(
                    t('checkoutPage.step3a.form.houseNr.label'),
                    true
                  )}
                  placeholder={t(
                    'checkoutPage.step3a.form.houseNr.placeholder'
                  )}
                />
              )}
            </Field>
          </div>
        </div>

        <div className="checkout-fieldset">
          <div className="d-block checkout-field">
            <Field name="postCode">
              {(props: FieldProps<PostCode, FormValues>) => (
                <CustomInput
                  {...props}
                  label={getRequiredOrNot(
                    t('checkoutPage.step3a.form.postCode.label'),
                    true
                  )}
                  placeholder={t(
                    'checkoutPage.step3a.form.postCode.placeholder'
                  )}
                />
              )}
            </Field>
          </div>
          <div className="d-block checkout-field">
            <Field name="city">
              {(props: FieldProps<City, FormValues>) => (
                <CustomInput
                  {...props}
                  label={getRequiredOrNot(
                    t('checkoutPage.step3a.form.city.label'),
                    true
                  )}
                  placeholder={t('checkoutPage.step3a.form.city.placeholder')}
                />
              )}
            </Field>
          </div>
        </div>
      </div>
    </div>
  )
}

const getRequiredOrNot = (label: string, required?: boolean) =>
  `${required ? '* ' : ''}${label}`

export const DELIVERY_METHODS: Record<DeliveryType, Price> = {
  FREE_PARCEL: 10,
  PICK_UP: 0
}

export default OrderDetails
