/* eslint-disable no-use-before-define */
import { Boundary } from 'components/common'
import { useDocumentTitle, useScrollTop } from 'hooks'
import React, { useEffect, useState } from 'react'
import { useTranslation } from 'react-i18next'
import { useSelector } from 'react-redux'
import { Redirect } from 'react-router-dom'
import { CHECKOUT_STEP_1 } from 'constants/routes'
import Flexbox from 'components2/Flexbox'
import { Formik, Form as FormikForm } from 'formik'
import Actions from 'views/checkout/commons/Actions'
import { CustomerType } from 'types'
import WithCheckout from '../commons/WithCheckout'
import StepTracker from '../commons/StepTracker'
import Checkout from '../commons/Checkout'
import Wrapper from '../commons/Wrapper'
import Form from './Form'
import useFormSubmit from './useFormSubmit'

const Step2 = () => {
  const { t } = useTranslation()

  const { customerType: customerTypeReducer } = useSelector((store) => ({
    customerType: store.NEWcustomerType
  }))

  const [customerType, setCustomerType] = useState<CustomerType>(
    customerTypeReducer || 'COMPANY'
  )
  const { onSubmitForm, getInitialValues, getSchema } = useFormSubmit()
  const { basket } = useSelector((store) => ({
    basket: store.basket
  }))

  useDocumentTitle('Check Out Step 2 | Salinaka')
  useScrollTop()

  if (basket.length === 0) {
    return <Redirect to={CHECKOUT_STEP_1} />
  }

  return (
    <Boundary>
      <Checkout>
        <StepTracker current={2} />

        <Wrapper>
          <h3 className="text-center">{t('checkoutPage.step2.title')}</h3>

          {!customerTypeReducer && (
            <Flexbox justifyContent="center" alignItems="center">
              <button
                type="button"
                onClick={() => setCustomerType('COMPANY')}
                disabled={customerType === 'COMPANY'}
              >
                {t('checkoutPage.step2.customerTypes.company')}
              </button>

              <button
                type="button"
                onClick={() => setCustomerType('INDIVIDUAL')}
                disabled={customerType === 'INDIVIDUAL'}
              >
                {t('checkoutPage.step2.customerTypes.individual')}
              </button>
            </Flexbox>
          )}

          <Formik
            initialValues={getInitialValues()}
            validateOnChange
            validationSchema={getSchema(customerType)}
            onSubmit={(form) => onSubmitForm(form, customerType)}
          >
            {({ handleSubmit }) => (
              <FormikForm onSubmit={handleSubmit}>
                <Form customerType={customerType} />
                <Actions step={2} />
              </FormikForm>
            )}
          </Formik>
        </Wrapper>
      </Checkout>
    </Boundary>
  )
}

export default WithCheckout(Step2)
