import Basket from 'components/common/Basket'
import { Footer, Navigation } from 'components/common'
import * as ROUTES from 'constants/routes'
import { createBrowserHistory } from 'history'
import React from 'react'
import { useSelector } from 'react-redux'
import { Route, Router, Switch } from 'react-router-dom'
import {
  AddProduct,
  CheckoutResult,
  CheckoutStep1,
  CheckoutStep2,
  CheckoutStep3,
  CheckoutStep3a,
  CheckoutStep4,
  Dashboard,
  EditAccount,
  EditProduct,
  FeaturedProducts,
  ForgotPassword,
  Home,
  PageNotFound,
  Products,
  RecommendedProducts,
  Search,
  Shop,
  SignIn,
  SignUp,
  UserAccount,
  ViewProduct
} from 'views'
import AdminRoute from './AdminRoute'
import ClientRoute from './ClientRoute'
import PublicRoute from './PublicRoute'

export const history = createBrowserHistory()

// const PageNotFound = lazy(() => import('views/PageNotFound'))

const AppRouter = () => {
  const store = useSelector((stor) => ({
    ...stor
  }))

  console.log('+', store)

  return (
    <Router history={history}>
      <>
        <Navigation />
        <Basket />
        <Switch>
          <Route component={Search} exact path={ROUTES.SEARCH} />
          <Route component={Home} exact path={ROUTES.HOME} />
          <Route component={Shop} exact path={ROUTES.SHOP} />
          <Route
            component={FeaturedProducts}
            exact
            path={ROUTES.FEATURED_PRODUCTS}
          />
          <Route
            component={RecommendedProducts}
            exact
            path={ROUTES.RECOMMENDED_PRODUCTS}
          />
          <PublicRoute component={SignUp} path={ROUTES.SIGNUP} />
          <PublicRoute component={SignIn} exact path={ROUTES.SIGNIN} />
          <PublicRoute
            component={ForgotPassword}
            path={ROUTES.FORGOT_PASSWORD}
          />
          <Route component={ViewProduct} path={ROUTES.VIEW_PRODUCT} />
          <ClientRoute component={UserAccount} exact path={ROUTES.ACCOUNT} />
          <ClientRoute
            component={EditAccount}
            exact
            path={ROUTES.ACCOUNT_EDIT}
          />

          <ClientRoute
            component={CheckoutStep1}
            path={ROUTES.CHECKOUT_STEP_1}
          />
          <ClientRoute
            component={CheckoutStep2}
            path={ROUTES.CHECKOUT_STEP_2}
          />
          <ClientRoute
            component={CheckoutStep3}
            path={ROUTES.CHECKOUT_STEP_3}
          />
          <ClientRoute
            component={CheckoutStep3a}
            path={ROUTES.CHECKOUT_STEP_3A}
          />
          <ClientRoute
            component={CheckoutStep4}
            path={ROUTES.CHECKOUT_STEP_4}
          />
          <ClientRoute
            component={CheckoutResult}
            path={ROUTES.CHECKOUT_RESULT}
          />

          <AdminRoute
            component={Dashboard}
            exact
            path={ROUTES.ADMIN_DASHBOARD}
          />
          <AdminRoute component={Products} path={ROUTES.ADMIN_PRODUCTS} />
          <AdminRoute component={AddProduct} path={ROUTES.ADD_PRODUCT} />
          <AdminRoute
            component={EditProduct}
            path={`${ROUTES.EDIT_PRODUCT}/:id`}
          />
          <PublicRoute component={PageNotFound} />
        </Switch>
        <Footer />
      </>
    </Router>
  )
}

export default AppRouter
