import { Boundary } from 'components/common'
import { useDocumentTitle, useScrollTop } from 'hooks'
import React from 'react'
import { useTranslation } from 'react-i18next'
import { Total } from 'types'
import { Formik, Form as FormikForm } from 'formik'
import { useSelector } from 'react-redux'
import { Redirect } from 'react-router-dom'
import { CHECKOUT_STEP_2 } from 'constants/routes'
import Actions from '../commons/Actions'
import WithCheckout from '../commons/WithCheckout'
import StepTracker from '../commons/StepTracker'
import Checkout from '../commons/Checkout'
import Wrapper from '../commons/Wrapper'
import ShippingTotal from '../commons/ShippingTotal'
/* eslint-disable no-use-before-define */
/* eslint-disable jsx-a11y/label-has-associated-control */

import useFormSubmit, { formSchema } from './form'
import OrderDetails, { DELIVERY_METHODS } from './OrderDetails'

type Props = {
  total: Total
}

const Step3 = ({ total }: Props) => {
  const {
    contactDetails: { email, mobile, fullname }
  } = useSelector((store) => ({
    contactDetails: store.NEWcontactDetails
  }))
  const { onSubmitForm, getInitialValues } = useFormSubmit()
  const { t } = useTranslation()

  useDocumentTitle('Check Out Step 2 | Salinaka')
  useScrollTop()

  if (!email || !mobile || !fullname) {
    return <Redirect to={CHECKOUT_STEP_2} />
  }

  return (
    <Boundary>
      <Checkout>
        <StepTracker current={3} />

        <Wrapper>
          <h3 className="text-center">{t('checkoutPage.step3.title')}</h3>

          <Formik
            initialValues={getInitialValues()}
            validateOnChange
            validationSchema={formSchema}
            onSubmit={onSubmitForm}
          >
            {({ handleSubmit, values }) => {
              const delivery = values.deliveryType
                ? DELIVERY_METHODS[values.deliveryType]
                : 0

              return (
                <FormikForm onSubmit={handleSubmit}>
                  <OrderDetails />

                  <ShippingTotal
                    total={{
                      ...total,
                      delivery,
                      sum: total.products + delivery
                    }}
                  />

                  <Actions step={3} />
                </FormikForm>
              )
            }}
          </Formik>
        </Wrapper>
      </Checkout>
    </Boundary>
  )
}

export default WithCheckout(Step3)
