import {
  ADD_QTY_ITEM,
  ADD_TO_BASKET,
  CLEAR_BASKET,
  MINUS_QTY_ITEM,
  REMOVE_FROM_BASKET,
  SET_BASKET_ITEMS,
  SET_ORDERS_ITEMS
} from 'constants/constants'
import product from 'models/product'

type Action = {
  type: typeof SET_ORDERS_ITEMS
  payload: any[]
}

export default (state: any[] = [], action: Action) => {
  switch (action.type) {
    case SET_ORDERS_ITEMS:
      return action.payload

    default:
      return state
  }
}
