/* eslint-disable import/extensions */
/* eslint-disable react/jsx-wrap-multilines */
import { LoadingOutlined } from '@ant-design/icons'
import { useDocumentTitle, useScrollTop } from 'hooks'
import { Product } from 'models/product'
import React, { lazy, Suspense } from 'react'
import { useTranslation } from 'react-i18next'
import { useDispatch, useSelector } from 'react-redux'
import { withRouter } from 'react-router-dom'
import { addProduct } from 'redux/actions/productActions'

const ProductForm = lazy(() => import('./components/ProductForm'))

const AddProduct = () => {
  const { t } = useTranslation()
  useScrollTop()
  useDocumentTitle('Add New Product | Salinaka')
  const isLoading = useSelector<any, boolean>((state) => state.app.loading)
  const dispatch = useDispatch()

  const onSubmit = (product: Product) => {
    dispatch(addProduct(product))
  }

  return (
    <div className="product-form-container">
      <h2>{t('addProductPage.title')}</h2>
      <Suspense
        fallback={
          <div className="loader" style={{ minHeight: '80vh' }}>
            <h6>{t('common.loading')}</h6>
            <br />
            <LoadingOutlined />
          </div>
        }
      >
        <ProductForm isLoading={isLoading} onSubmit={onSubmit} />
      </Suspense>
    </div>
  )
}

export default withRouter(AddProduct)
