import { Boundary } from 'components/common'
import { useDocumentTitle, useScrollTop } from 'hooks'
import React from 'react'
import { useTranslation } from 'react-i18next'
import { Formik, Form as FormikForm } from 'formik'
import { Total } from 'types'
import WithCheckout from '../commons/WithCheckout'
import StepTracker from '../commons/StepTracker'
import Checkout from '../commons/Checkout'
import Wrapper from '../commons/Wrapper'
import ShippingTotal from '../commons/ShippingTotal'
import useFormSubmit, { formSchema } from './form'
import OrderDetails from './OrderDetails'
import Actions from '../commons/Actions'
import { DELIVERY_METHODS } from '../Step3/OrderDetails'

type Props = {
  total: Total
}

const Step3a = ({ total }: Props) => {
  const { t } = useTranslation()
  const { onSubmitForm, getInitialValues } = useFormSubmit()

  useDocumentTitle('Check Out Step 2 | Salinaka')
  useScrollTop()

  return (
    <Boundary>
      <Checkout>
        <StepTracker current={3} />

        <Wrapper>
          <h3 className="text-center">{t('checkoutPage.step3a.title')}</h3>

          <Formik
            initialValues={getInitialValues()}
            validateOnChange
            validationSchema={formSchema}
            onSubmit={onSubmitForm}
          >
            {({ handleSubmit }) => {
              const delivery = DELIVERY_METHODS.FREE_PARCEL

              return (
                <FormikForm onSubmit={handleSubmit}>
                  <OrderDetails />

                  <ShippingTotal
                    total={{
                      ...total,
                      delivery,
                      sum: total.products + delivery
                    }}
                  />
                  <Actions step={101} />
                </FormikForm>
              )
            }}
          </Formik>
        </Wrapper>
      </Checkout>
    </Boundary>
  )
}

export default WithCheckout(Step3a)
