import {
  ADD_QTY_ITEM,
  ADD_TO_BASKET,
  CLEAR_BASKET,
  MINUS_QTY_ITEM,
  REMOVE_FROM_BASKET,
  SET_BASKET_ITEMS
} from 'constants/constants'
import product from 'models/product'
import { BasketProduct } from 'types'

type Action =
  | {
      type: typeof ADD_QTY_ITEM
      payload: BasketProduct
    }
  | {
      type: typeof ADD_TO_BASKET
      payload: BasketProduct
    }
  | {
      type: typeof CLEAR_BASKET
    }
  | {
      type: typeof MINUS_QTY_ITEM
      payload: BasketProduct
    }
  | {
      type: typeof REMOVE_FROM_BASKET
      payload: BasketProduct
    }
  | {
      type: typeof SET_BASKET_ITEMS
      payload: BasketProduct[]
    }

export default (state: BasketProduct[] = [], action: Action) => {
  switch (action.type) {
    case SET_BASKET_ITEMS:
      return action.payload
    case ADD_TO_BASKET:
      const sameProduct = state.find((product) => {
        if (product.variants.length !== 0) {
          return (
            product.id === action.payload.id &&
            product.variant === action.payload.variant
          )
        }

        return product.id === action.payload.id
      })

      return sameProduct ? state : [action.payload, ...state]
    case REMOVE_FROM_BASKET:
      const theSameProduct = state.find((product) => {
        if (product.variants.length !== 0) {
          return (
            product.id == action.payload.id &&
            product.variant === action.payload.variant
          )
        }

        return product.id == action.payload.id
      })

      return state.filter((product) => product !== theSameProduct)
    case CLEAR_BASKET:
      return []
    case ADD_QTY_ITEM:
      return state.map((product) => {
        if (product.variants.length !== 0) {
          if (
            product.id === action.payload.id &&
            product.variant === action.payload.variant
          ) {
            return {
              ...product,
              quantity: product.quantity + 1
            }
          }
          return product
        } else {
          if (product.id === action.payload.id) {
            return {
              ...product,
              quantity: product.quantity + 1
            }
          }

          return product
        }
      })
    case MINUS_QTY_ITEM:
      return state.map((product) => {
        if (product.variants.length !== 0) {
          if (
            product.id === action.payload.id &&
            product.variant === action.payload.variant
          ) {
            return {
              ...product,
              quantity: product.quantity - 1
            }
          }
          return product
        } else {
          if (product.id === action.payload.id) {
            return {
              ...product,
              quantity: product.quantity - 1
            }
          }

          return product
        }
      })
    default:
      return state
  }
}
