/* eslint-disable react/jsx-wrap-multilines */
import { FEATURED_PRODUCTS, RECOMMENDED_PRODUCTS, SHOP } from 'constants/routes'
import {
  useDocumentTitle,
  useFeaturedProducts,
  useRecommendedProducts,
  useScrollTop
} from 'hooks'
import bannerImg from 'images/banner-girl.png'
import React from 'react'
import { Link } from 'react-router-dom'
import Page from 'components2/Page'
import Banner from 'components2/Banner'
import Display from 'components2/Display'
import { useTranslation } from 'react-i18next'
import { ArrowRightOutlined } from '@ant-design/icons'
import Box from 'components2/Box'

const Home = () => {
  const { t } = useTranslation()

  useDocumentTitle('Salinaka | Home')
  useScrollTop()

  const {
    featuredProducts,
    fetchFeaturedProducts,
    isLoading: isLoadingFeatured,
    error: errorFeatured
  } = useFeaturedProducts(6)
  const {
    recommendedProducts,
    fetchRecommendedProducts,
    isLoading: isLoadingRecommended,
    error: errorRecommended
  } = useRecommendedProducts(6)

  return (
    <Page>
      <Banner
        img={bannerImg}
        description={t('homePage.banner.description')}
        title={
          <h1 className="text-thin">
            <strong>See</strong>
            &nbsp;everything with&nbsp;
            <strong>Clarity</strong>
          </h1>
        }
      >
        <br />
        <Link to={SHOP} className="button">
          {t('homePage.banner.buttonLabel')}
          <Box marginLeft="0.5rem">
            <ArrowRightOutlined />
          </Box>
        </Link>
      </Banner>

      <Display
        title={t('homePage.featuredProducts.title')}
        link={
          <Link to={FEATURED_PRODUCTS}>
            {t('homePage.featuredProducts.buttonLabel')}
          </Link>
        }
        error={errorFeatured}
        isLoading={isLoadingFeatured}
        fetchProducts={fetchFeaturedProducts}
        products={featuredProducts}
      />

      <Display
        title={t('homePage.recommendedProducts.title')}
        link={
          <Link to={RECOMMENDED_PRODUCTS}>
            {t('homePage.featuredProducts.buttonLabel')}
          </Link>
        }
        error={errorRecommended}
        isLoading={isLoadingRecommended}
        fetchProducts={fetchRecommendedProducts}
        products={recommendedProducts}
      />
    </Page>
  )
}

export default Home
