/* eslint-disable no-use-before-define */
/* eslint-disable jsx-a11y/label-has-associated-control */
import { CustomInput, CustomMobileInput } from 'components/formik'
import { Field, FieldProps, FormikValues, useFormikContext } from 'formik'
import getRequiredOrNot from 'helpers/getRequiredOrNot'
import React, { useEffect } from 'react'
import { useTranslation } from 'react-i18next'
import { useSelector } from 'react-redux'
import { City, CustomerType, Email, Fullname, Postcode, Street } from 'types'
import { FormValues } from './useFormSubmit'

type Props = {
  customerType: CustomerType
}
const Form = ({ customerType }: Props) => {
  const { t } = useTranslation()
  const { values, resetForm, initialValues } = useFormikContext<FormikValues>()

  const { hasPreviousOrder } = useSelector((store) => ({
    hasPreviousOrder: store.orders.length > 0
  }))

  useEffect(() => {
    resetForm()
  }, [customerType])

  return (
    <div className="checkout-shipping-wrapper">
      <div className="checkout-shipping-form">
        <div className="checkout-fieldset">
          <div className="d-block checkout-field">
            <Field name="fullname" type="text">
              {(props: FieldProps<Fullname, FormValues>) => {
                const base = customerType === 'INDIVIDUAL' ? 'name' : 'company'

                return (
                  <CustomInput
                    {...props}
                    style={{ textTransform: 'capitalize' }}
                    disabled={hasPreviousOrder && initialValues.fullname}
                    label={getRequiredOrNot(
                      t(`checkoutPage.step2.form.${base}.label`),
                      true
                    )}
                    placeholder={t(
                      `checkoutPage.step2.form.${base}.placeholder`
                    )}
                  />
                )
              }}
            </Field>
          </div>

          {customerType === 'COMPANY' && (
            <div className="d-block checkout-field">
              <Field name="nip" type="text">
                {(props: FieldProps<Fullname, FormValues>) => (
                  <CustomInput
                    {...props}
                    disabled={hasPreviousOrder && initialValues.nip}
                    label={getRequiredOrNot(
                      t('checkoutPage.step2.form.nip.label'),
                      true
                    )}
                    placeholder={t('checkoutPage.step2.form.nip.placeholder')}
                  />
                )}
              </Field>
            </div>
          )}
        </div>

        <div className="checkout-fieldset">
          <div className="d-block checkout-field">
            <Field name="street" type="text">
              {(props: FieldProps<Street, FormValues>) => (
                <CustomInput
                  {...props}
                  disabled={hasPreviousOrder && initialValues.street}
                  label={getRequiredOrNot(
                    t('checkoutPage.step2.form.street.label'),
                    true
                  )}
                  placeholder={t('checkoutPage.step2.form.street.placeholder')}
                />
              )}
            </Field>
          </div>
        </div>

        <div className="checkout-fieldset">
          <div className="d-block checkout-field">
            <Field name="postCode" type="text">
              {(props: FieldProps<Postcode, FormValues>) => (
                <CustomInput
                  {...props}
                  disabled={hasPreviousOrder && initialValues.postCode}
                  label={getRequiredOrNot(
                    t('checkoutPage.step2.form.postCode.label'),
                    true
                  )}
                  placeholder={t(
                    'checkoutPage.step2.form.postCode.placeholder'
                  )}
                />
              )}
            </Field>
          </div>
          <div className="d-block checkout-field">
            <Field name="city" type="text">
              {(props: FieldProps<City, FormValues>) => (
                <CustomInput
                  {...props}
                  disabled={hasPreviousOrder && initialValues.city}
                  label={getRequiredOrNot(
                    t('checkoutPage.step2.form.city.label'),
                    true
                  )}
                  placeholder={t('checkoutPage.step2.form.city.placeholder')}
                />
              )}
            </Field>
          </div>
        </div>

        <div className="checkout-fieldset">
          <div className="d-block checkout-field">
            <Field name="email" type="email">
              {(props: FieldProps<Email, FormValues>) => (
                <CustomInput
                  {...props}
                  disabled
                  label={getRequiredOrNot(
                    t('checkoutPage.step2.form.email.label'),
                    true
                  )}
                  placeholder={t('checkoutPage.step2.form.email.placeholder')}
                />
              )}
            </Field>
          </div>
          <div className="d-block checkout-field">
            <CustomMobileInput
              label={getRequiredOrNot(
                t('checkoutPage.step2.form.phone.label'),
                true
              )}
              disabled={hasPreviousOrder && initialValues.mobile.value}
              name="mobile"
              defaultValue={values.mobile}
            />
          </div>
        </div>
      </div>
    </div>
  )
}

export default Form
