/* eslint-disable implicit-arrow-linebreak */
import { displayActionMessage } from 'helpers/utils'
import { useTranslation } from 'react-i18next'
import { useDispatch, useSelector } from 'react-redux'
import {
  addToBasket as dispatchAddToBasket,
  removeFromBasket
} from 'redux/actions/basketActions'
import { BasketProduct } from 'types'

const useBasket = () => {
  const { t } = useTranslation()

  const { basket } = useSelector<any, { basket: BasketProduct[] }>((state) => ({
    basket: state.basket
  }))
  const dispatch = useDispatch()

  const isItemOnBasket = (id: string, variant: string | null) =>
    !!basket.find((item) => {
      if (item.variants.length !== 0) {
        return item.id === id && item.variant === variant
      }
      return item.id === id
    })

  const addToBasket = (product: BasketProduct) => {
    if (isItemOnBasket(product.id, product.variant)) {
      dispatch(removeFromBasket(product))
      displayActionMessage(t('common.toasts.removeProductFromBasket'), 'info')
    } else {
      dispatch(dispatchAddToBasket(product))
      displayActionMessage(t('common.toasts.addedProductToBasket'), 'success')
    }
  }

  return { basket, isItemOnBasket, addToBasket }
}

export default useBasket
