import Flexbox from 'components2/Flexbox'
import { DeliveryType, Price } from 'types'
import React from 'react'
import { Field, FieldProps } from 'formik'
import { useTranslation } from 'react-i18next'
import { displayMoney } from 'helpers/utils'
import CheckboxField from 'components2/CheckboxField'
import { FormValues } from './form'

const OrderDetails = () => {
  const { t } = useTranslation()

  return (
    <div className="checkout-shipping-wrapper">
      <div className="checkout-shipping-form">
        <Flexbox
          margin="0 1.2rem"
          flexDirection="column"
          className="checkout-fieldset"
        >
          {Object.entries(DELIVERY_METHODS).map(([key, price], index) => (
            <Field name="deliveryType" key={key}>
              {(props: FieldProps<DeliveryType, FormValues>) => (
                <CheckboxField
                  {...props}
                  id={key}
                  isFirst={index === 0}
                  title={t(`checkoutPage.step3.form.delivery.${key}.title`)}
                  subtitle={t(`checkoutPage.step3.form.delivery.${key}.time`)}
                  val={displayMoney(price)}
                  fieldLabel={t('checkoutPage.step3.form.delivery.label')}
                />
              )}
            </Field>
          ))}
        </Flexbox>
      </div>
    </div>
  )
}

export const DELIVERY_METHODS: Record<DeliveryType, Price> = {
  FREE_PARCEL: 10,
  PICK_UP: 0
}

export default OrderDetails
