import { SET_PREFERRED_PAYMENT_LOCALY } from 'constants/constants'
import { SetPreferredPaymentPayload } from 'redux/actions/new/prefferedPaymentActions'
import { Shipping } from 'types'

const initState = null

type Action = {
  type: typeof SET_PREFERRED_PAYMENT_LOCALY
  payload: SetPreferredPaymentPayload
}

const preferredPaymentReducer = (state = initState, action: Action) => {
  switch (action.type) {
    case SET_PREFERRED_PAYMENT_LOCALY:
      return action.payload

    default:
      return state
  }
}

export default preferredPaymentReducer
