import { ADD_ORDER_REMOTELY } from 'constants/constants'
import {
  Auth,
  BasketProduct,
  ContactDetails,
  DeliveryType,
  PaymentType,
  Shipping,
  Total
} from 'types'

export type AddOrderRemotelyPayload = {
  total: Total
  basket: BasketProduct[]
  auth: Auth
  paymentType: PaymentType
  deliveryType: DeliveryType
  contactDetails: ContactDetails
  shipping: Shipping | null
}
export const addOrderRemotely = (payload: AddOrderRemotelyPayload) => ({
  type: ADD_ORDER_REMOTELY,
  payload
})
