import { ArrowRightOutlined, LoadingOutlined } from '@ant-design/icons'
import { CustomInput } from 'components/formik'
import Button from 'components2/Button'
import { FORGOT_PASSWORD, SIGNUP } from 'constants/routes'
import { Field, FieldProps, Form, Formik } from 'formik'
import { useDocumentTitle, useScrollTop } from 'hooks'
import React, { useEffect } from 'react'
import { useTranslation } from 'react-i18next'
import { useDispatch, useSelector } from 'react-redux'
import { Link, useHistory } from 'react-router-dom'
import { setAuthenticating, setAuthStatus } from 'redux/actions/miscActions'
import { Email } from 'types'
import useForm, { FormValues } from './form'

const SignIn = () => {
  const { t } = useTranslation()
  const history = useHistory()
  const { authStatus, isAuthenticating } = useSelector((state) => ({
    authStatus: state.app.authStatus,
    isAuthenticating: state.app.isAuthenticating
  }))

  const dispatch = useDispatch()

  const { getInitialValues, getSchema, submitForm } = useForm()
  useScrollTop()
  useDocumentTitle('Sign In | Salinaka')

  useEffect(
    () => () => {
      dispatch(setAuthStatus())
      dispatch(setAuthenticating(false))
    },
    []
  )

  return (
    <div className="auth-content">
      {authStatus?.success && (
        <div className="loader">
          <h3 className="toast-success auth-success">
            {authStatus.message}
            <LoadingOutlined />
          </h3>
        </div>
      )}
      {!authStatus?.success && (
        <>
          {authStatus?.message && (
            <h5 className="text-center toast-error">{authStatus?.message}</h5>
          )}
          <div
            className={`auth ${
              authStatus?.message && !authStatus?.success && 'input-error'
            }`}
          >
            <div className="auth-main">
              <h3>{t('signInPage.title')}</h3>

              <Formik
                initialValues={getInitialValues()}
                validateOnChange
                validationSchema={getSchema()}
                onSubmit={submitForm}
              >
                {() => (
                  <Form>
                    <div className="auth-field">
                      <Field
                        disabled={isAuthenticating}
                        name="email"
                        type="email"
                      >
                        {(props: FieldProps<Email, FormValues>) => (
                          <CustomInput
                            label={t('signInPage.form.email.label')}
                            placeholder={t('signInPage.form.email.placeholder')}
                            {...props}
                          />
                        )}
                      </Field>
                    </div>
                    <div className="auth-field">
                      <Field
                        disabled={isAuthenticating}
                        name="password"
                        type="password"
                      >
                        {(props: FieldProps<Email, FormValues>) => (
                          <CustomInput
                            label={t('signInPage.form.password.label')}
                            placeholder={t(
                              'signInPage.form.password.placeholder'
                            )}
                            {...props}
                          />
                        )}
                      </Field>
                    </div>
                    <br />
                    <div className="auth-field auth-action">
                      <Link
                        onClick={(e) => {
                          if (isAuthenticating) e.preventDefault()
                        }}
                        style={{ textDecoration: 'underline' }}
                        to={FORGOT_PASSWORD}
                      >
                        <span>{t('signInPage.forgotPassword')}</span>
                      </Link>
                      <Button disabled={isAuthenticating} type="submit">
                        {t(
                          isAuthenticating
                            ? 'signInPage.signIning'
                            : 'signInPage.signIn'
                        )}
                        &nbsp;
                        {isAuthenticating ? (
                          <LoadingOutlined />
                        ) : (
                          <ArrowRightOutlined />
                        )}
                      </Button>
                    </div>
                  </Form>
                )}
              </Formik>
            </div>
          </div>
          <div className="auth-message">
            <span className="auth-info">
              <strong>{t('signInPage.notSignedup')}</strong>
            </span>

            <Button
              size="small"
              disabled={isAuthenticating}
              onClick={() => history.push(SIGNUP)}
              type="button"
              variant="ternary"
            >
              {t('signInPage.signUp')}
            </Button>
          </div>
        </>
      )}
    </div>
  )
}

export default SignIn
