import { ArrowRightOutlined, LoadingOutlined } from '@ant-design/icons'
import { CustomInput } from 'components/formik'
import Box from 'components2/Box'
import Button from 'components2/Button'
import { SIGNIN } from 'constants/routes'
import { Field, FieldProps, Form, Formik } from 'formik'
import { useDocumentTitle, useScrollTop } from 'hooks'
import React, { useEffect } from 'react'
import { useTranslation } from 'react-i18next'
import { useDispatch, useSelector } from 'react-redux'
import { useHistory } from 'react-router-dom'
import { setAuthenticating, setAuthStatus } from 'redux/actions/miscActions'
import { Email, Password } from 'types'
import useForm, { FormValues } from './form'

const SignUp = () => {
  const history = useHistory()
  const { t } = useTranslation()

  const { isAuthenticating, authStatus } = useSelector((state) => ({
    isAuthenticating: state.app.isAuthenticating,
    authStatus: state.app.authStatus
  }))

  const dispatch = useDispatch()
  const { submitForm, getInitialValues, getSchema } = useForm()

  useScrollTop()
  useDocumentTitle('Sign Up | Salinaka')

  useEffect(
    () => () => {
      dispatch(setAuthStatus())
      dispatch(setAuthenticating(false))
    },
    []
  )

  return (
    <div className="auth-content">
      {authStatus?.success && (
        <div className="loader">
          <h3 className="toast-success auth-success">
            {authStatus?.message}
            <LoadingOutlined />
          </h3>
        </div>
      )}

      {!authStatus?.success && (
        <>
          {authStatus?.message && (
            <h5 className="text-center toast-error">{authStatus?.message}</h5>
          )}
          <div
            className={`auth ${
              authStatus?.message && !authStatus?.success && 'input-error'
            }`}
          >
            <div className="auth-main">
              <h3>{t('signUpPage.title')}</h3>
              <Formik
                initialValues={getInitialValues()}
                validateOnChange
                validationSchema={getSchema()}
                onSubmit={submitForm}
              >
                {() => (
                  <Form>
                    <div className="auth-field">
                      <Field name="email" disabled={isAuthenticating}>
                        {(props: FieldProps<Email, FormValues>) => (
                          <CustomInput
                            {...props}
                            label={`* ${t('signUpPage.email.label')}`}
                            placeholder={t('signUpPage.email.placeholder')}
                          />
                        )}
                      </Field>
                    </div>
                    <div className="auth-field">
                      <Field disabled={isAuthenticating} name="password">
                        {(props: FieldProps<Password, FormValues>) => (
                          <CustomInput
                            type="password"
                            {...props}
                            label={`* ${t('signUpPage.password.label')}`}
                            placeholder={t('signUpPage.password.placeholder')}
                          />
                        )}
                      </Field>
                    </div>
                    <br />
                    <div className="auth-field auth-action auth-action-signup">
                      <Button disabled={isAuthenticating} type="submit">
                        {t(
                          isAuthenticating
                            ? 'signUpPage.signingUp'
                            : 'signUpPage.signUp'
                        )}
                        <Box marginLeft="0.5rem">
                          {isAuthenticating ? (
                            <LoadingOutlined />
                          ) : (
                            <ArrowRightOutlined />
                          )}
                        </Box>
                      </Button>
                    </div>
                  </Form>
                )}
              </Formik>
            </div>
          </div>
          <div className="auth-message">
            <span className="auth-info">
              <strong>{t('signUpPage.haveAccount')}</strong>
            </span>
            <Button
              disabled={isAuthenticating}
              onClick={() => history.push(SIGNIN)}
              type="button"
              variant="ternary"
              size="small"
            >
              {t('signUpPage.signIn')}
            </Button>
          </div>
        </>
      )}
    </div>
  )
}

export default SignUp
