/* eslint-disable indent */
import { ADD_ORDER_REMOTELY } from 'constants/constants'
import { displayActionMessage } from 'helpers/utils'
import { call, put } from 'redux-saga/effects'
import { clearBasket } from 'redux/actions/basketActions'
import { AddOrderRemotelyPayload } from 'redux/actions/checkoutActions'
import { setLoading, setRequestStatus } from 'redux/actions/miscActions'
import { setOrdersItems } from 'redux/actions/ordersActions'
import { history } from 'routers/AppRouter'
import firebase from 'services/firebase'

function* initRequest() {
  yield put(setLoading(true))
  yield put(setRequestStatus(null))
}

function* handleError(e) {
  yield put(setLoading(false))
  yield put(setRequestStatus(e?.message || 'Failed to fetch products'))
  console.log('ERROR: ', e)
}

function* handleAction(location, message, status) {
  if (location) yield call(history.push, location)
  yield call(displayActionMessage, message, status)
}

type Props = {
  type: typeof ADD_ORDER_REMOTELY
  payload: AddOrderRemotelyPayload
}

function* orderSaga({ type, payload }: Props) {
  switch (type) {
    case ADD_ORDER_REMOTELY: {
      try {
        yield initRequest()
        //
        const orderId: string = yield call(firebase.generateKey)
        const userId = payload.auth.id

        //
        const order = {
          products: payload.basket.map((product) => ({
            id: product.id,
            variant: product.variant,
            price: product.price,
            quantity: product.quantity
          })),
          total: payload.total,
          dateCreated: new Date(),
          paymentType: payload.paymentType,
          deliveryType: payload.deliveryType,
          contactDetails: payload.contactDetails,
          shipping: payload.deliveryType === 'PICK_UP' ? null : payload.shipping
        }
        yield call(firebase.addOrder, orderId, order)

        //
        const profileOrder = order
        yield call(firebase.addOrderToProfile, userId, orderId, profileOrder)

        //
        const userOrders = yield call(firebase.getUserOrders, userId)
        yield put(setOrdersItems(userOrders))

        //
        yield put(clearBasket())
      } catch (e) {
        console.log('addOrderError in Saga', e)
      }
      break
    }

    default: {
      throw new Error(`Unexpected action type ${type}`)
    }
  }
}

export default orderSaga
