/* eslint-disable react/no-array-index-key */
/* eslint-disable react/jsx-one-expression-per-line */
/* eslint-disable max-len */
import { Boundary } from 'components/common'
import { CHECKOUT_STEP_1 } from 'constants/routes'
import { useDidMount, useModal } from 'hooks'
import React, { Fragment, useEffect } from 'react'
import { useTranslation } from 'react-i18next'
import { useDispatch, useSelector } from 'react-redux'
import { useHistory } from 'react-router-dom'
import { clearBasket } from 'redux/actions/basketActions'
import Flexbox from 'components2/Flexbox'
import Separator from 'components2/Separator'
import { BasketProduct } from 'types'
import firebase from 'services/firebase'
import Modal from './Modal'
import Header from './Header'
import Checkout from './Checkout'
import BasketItem from './BasketItem'

const Basket = () => {
  const { t } = useTranslation()
  const { isOpenModal, onOpenModal, onCloseModal } = useModal()

  const { basket, user } = useSelector<
    any,
    { basket: BasketProduct[]; user: any }
  >((state) => ({
    basket: state.basket,
    user: state.auth
  }))
  const history = useHistory()
  const dispatch = useDispatch()
  const didMount = useDidMount()

  useEffect(() => {
    if (didMount && firebase.auth.currentUser && basket.length !== 0) {
      firebase
        .saveBasketItems(basket, firebase.auth.currentUser.uid)
        .then(() => {
          console.log('Item saved to basket')
        })
        .catch((e: any) => {
          console.log(e)
        })
    }
  }, [basket.length])

  const onCheckOut = () => {
    if (basket.length !== 0 && user) {
      document.body.classList.remove('is-basket-open')
      history.push(CHECKOUT_STEP_1)
    } else {
      onOpenModal()
    }
  }

  const onClearBasket = () => {
    if (basket.length !== 0) {
      dispatch(clearBasket())
    }
  }

  if (user && user.role === 'ADMIN') return null

  const isBasketEmpty = basket.length === 0

  return (
    <Boundary>
      <Modal isOpenModal={isOpenModal} onCloseModal={onCloseModal} />

      <div className="basket">
        <Header basket={basket} onClearBasket={onClearBasket} />

        <Flexbox
          flexGrow={1}
          flexDirection="column"
          overflowY="scroll"
          padding="s-size"
        >
          {isBasketEmpty && (
            <Flexbox alignItems="center" justifyContent="center" flexGrow={1}>
              <h5 className="basket-empty-msg">{t('common.basket.empty')}</h5>
            </Flexbox>
          )}

          {basket.map((product, i) => (
            <Fragment
              key={`${product.id}${
                product.variant ? `_${product.variant}` : ''
              }`}
            >
              <BasketItem product={product} />
              {basket.length - 1 !== i && <Separator />}
            </Fragment>
          ))}
        </Flexbox>

        <Checkout basket={basket} onCheckOut={onCheckOut} />
      </div>
    </Boundary>
  )
}

export default Basket
