import React from 'react'
import styled from 'styled-components'
import { ProductBase } from 'types'
import ProductTile from './ProductTile'

type Props<T> = {
  skeletonCount?: number
  products: T[]
  full?: boolean
}

function ProductsGrid<T extends ProductBase>({
  products,
  skeletonCount = 3,
  full
}: Props<T>) {
  const productsLoaded = products.length !== 0

  let productsToBeDisplayed = new Array(skeletonCount).fill({})
  if (productsLoaded) {
    productsToBeDisplayed = products

    if (!full) {
      productsToBeDisplayed = productsToBeDisplayed.slice(0, 3)
    }
  }

  return (
    <Container>
      {productsToBeDisplayed.map((product) => (
        <ProductTile key={product.id} product={product} />
      ))}
    </Container>
  )
}

const GRID_GAP = '3rem'

const Container = styled.div`
  display: grid;
  grid-template-columns: repeat(auto-fit, 38rem);
  grid-gap: ${GRID_GAP};

  /* @include mobile { */
  /* grid-template-columns: repeat(2, 1fr) !important; */
  /* } */
`

export default ProductsGrid
