/* eslint-disable operator-linebreak */
/* eslint-disable implicit-arrow-linebreak */
/* eslint-disable no-alert */
/* eslint-disable jsx-a11y/label-has-associated-control */
import { CheckOutlined, LoadingOutlined } from '@ant-design/icons'
import { ImageLoader } from 'components/common'
import {
  CustomCreatableSelect,
  CustomInput,
  CustomTextarea
} from 'components/formik'
import { Field, Form, Formik } from 'formik'
import { useFileHandler } from 'hooks'
import React from 'react'
import productSchema, { Product } from 'models/product'
import { Product } from 'src/models/product'
import { useTranslation } from 'react-i18next'

type Props = {
  product?: Product
  isLoading?: boolean
  onSubmit: (product: Product) => void
}

const ProductForm = ({ product, onSubmit, isLoading }: Props) => {
  const { t } = useTranslation()

  const initFormikValues = {
    name: product?.name || '',
    price: product?.price || 0,
    description: product?.description || '',
    variants: product?.variants || [],
    isFeatured: product?.isFeatured || false,
    isRecommended: product?.isRecommended || false,
    gallery: product?.gallery || [],
    quantity: 0
  }

  const { imageFile, isFileLoading, onFileChange, removeImage } =
    useFileHandler({
      gallery: product?.gallery || []
    })

  const onSubmitForm = (form) => {
    if (
      initFormikValues.gallery.length !== 0 ||
      imageFile.gallery.length !== 0
    ) {
      const vals = {
        ...form,
        name_lower: form.name.toLowerCase(),
        dateAdded: new Date().getTime(),
        gallery: imageFile.gallery
      }

      onSubmit(vals)
    } else {
      alert('Dodaj co najmniej jedno zdjcie!')
    }
  }

  return (
    <div>
      <Formik
        initialValues={initFormikValues}
        validateOnChange
        validationSchema={productSchema}
        onSubmit={onSubmitForm}
      >
        {({ values, setValues }) => (
          <Form className="product-form">
            <div className="product-form-inputs">
              <div className="product-form-field">
                <Field
                  disabled={isLoading}
                  name="name"
                  type="text"
                  label={`* ${t('addProductPage.form.productName')}`}
                  component={CustomInput}
                />
              </div>

              <div className="product-form-field">
                <Field
                  disabled={isLoading}
                  name="description"
                  id="description"
                  rows={3}
                  label={`* ${t('addProductPage.form.description')}`}
                  component={CustomTextarea}
                />
              </div>

              <div className="product-form-field">
                <Field
                  disabled={isLoading}
                  name="price"
                  id="price"
                  type="number"
                  label={`* ${t('addProductPage.form.price')}`}
                  component={CustomInput}
                />
              </div>

              <div className="product-form-field">
                <CustomCreatableSelect
                  defaultValue={values.variants.map((key) => ({
                    value: key,
                    label: key
                  }))}
                  name="variants"
                  iid="variants"
                  type="text"
                  isMulti
                  disabled={isLoading}
                  placeholder={t('addProductPage.form.variants.placeholder')}
                  label={`* ${t('addProductPage.form.variants.label')}`}
                />
              </div>

              <div className="product-form-field">
                <span className="d-block padding-s">
                  {t('addProductPage.form.gallery.label')}
                </span>
                {!isFileLoading && (
                  <label htmlFor="product-input-file-collection">
                    <input
                      disabled={isLoading}
                      hidden
                      id="product-input-file-collection"
                      multiple
                      onChange={(e) =>
                        onFileChange(e, {
                          name: 'gallery',
                          type: 'multiple'
                        })
                      }
                      readOnly={isLoading}
                      type="file"
                    />
                    {t('addProductPage.form.gallery.placeholder')}
                  </label>
                )}
              </div>

              <div className="product-form-collection">
                <>
                  {imageFile.gallery.length >= 1 &&
                    imageFile.gallery.map((image) => (
                      <div
                        className="product-form-collection-image"
                        key={image.id}
                      >
                        <ImageLoader alt="" src={image.url} />
                        <button
                          className="product-form-delete-image"
                          onClick={() =>
                            removeImage({
                              id: image.id,
                              name: 'gallery'
                            })
                          }
                          title="Delete Image"
                          type="button"
                        >
                          XXX
                        </button>
                      </div>
                    ))}
                </>
              </div>
              <br />
              <div className="d-flex">
                <div className="product-form-field">
                  <input
                    checked={values.isFeatured}
                    id="featured"
                    onChange={(e) =>
                      setValues({ ...values, isFeatured: e.target.checked })
                    }
                    type="checkbox"
                  />
                  <label htmlFor="featured">
                    <h5 className="d-flex-grow-1 margin-0">
                      {t('addProductPage.form.featured.label')}
                    </h5>
                  </label>
                </div>
                <div className="product-form-field">
                  <input
                    checked={values.isRecommended}
                    id="recommended"
                    onChange={(e) =>
                      setValues({
                        ...values,
                        isRecommended: e.target.checked
                      })
                    }
                    type="checkbox"
                  />
                  <label htmlFor="recommended">
                    <h5 className="d-flex-grow-1 margin-0">
                      {t('addProductPage.form.recommended.label')}
                    </h5>
                  </label>
                </div>
              </div>
              <br />
              <br />
              <br />
              <div className="product-form-field product-form-submit">
                <button className="button" disabled={isLoading} type="submit">
                  {isLoading ? <LoadingOutlined /> : <CheckOutlined />}
                  &nbsp;
                  {isLoading
                    ? 'Saving Product'
                    : t('addProductPage.actions.save')}
                </button>
              </div>
            </div>
          </Form>
        )}
      </Formik>
    </div>
  )
}

export default ProductForm
