/* eslint-disable indent */
import { ImageLoader } from 'components/common'
import Flexbox from 'components2/Flexbox'
import { displayDate } from 'helpers/utils'
import React from 'react'
import { useTranslation } from 'react-i18next'
import { useSelector } from 'react-redux'
import { withRouter } from 'react-router-dom'
import SingleDetail from './SingleDetail'

const Profile = () => {
  const { t } = useTranslation()
  const { profile, contactDetails, custmerType } = useSelector((state) => ({
    profile: state.profile,
    contactDetails: state.NEWcontactDetails,
    custmerType: state.NEWcustomerType
  }))

  return (
    <Flexbox flexDirection="column" marginX="auto">
      <div className="user-profile-banner">
        <div className="user-profile-banner-wrapper">
          <ImageLoader
            alt="Banner"
            className="user-profile-banner-img"
            src={profile.banner}
          />
        </div>
        <div className="user-profile-avatar-wrapper">
          <ImageLoader
            alt="Avatar"
            className="user-profile-img"
            src={profile.avatar}
          />
        </div>
      </div>

      <div className="user-profile-details">
        <h2 className="user-profile-name">{contactDetails.fullname}</h2>
        <SingleDetail
          label={t('profilePage.account.fields.customerType')}
          value={
            custmerType
              ? t(`profilePage.account.customerTypes.${custmerType}`)
              : null
          }
        />

        <SingleDetail
          label={t('profilePage.account.fields.email')}
          value={contactDetails.email}
        />

        <SingleDetail
          label={t('profilePage.account.fields.phone')}
          value={contactDetails?.mobile?.value}
        />

        <SingleDetail
          label={t('profilePage.account.fields.street')}
          value={contactDetails.street}
        />

        <SingleDetail
          label={t('profilePage.account.fields.postCode')}
          value={contactDetails.postCode}
        />

        <SingleDetail
          label={t('profilePage.account.fields.city')}
          value={contactDetails.city}
        />

        <SingleDetail
          label={t('profilePage.account.fields.dateJoined')}
          value={displayDate(profile.dateJoined)}
        />
      </div>
    </Flexbox>
  )
}

export default withRouter(Profile)
