import { ArrowRightOutlined, ShopOutlined } from '@ant-design/icons'
import Box from 'components2/Box'
import Flexbox from 'components2/Flexbox'
import {
  CHECKOUT_STEP_1,
  CHECKOUT_STEP_2,
  CHECKOUT_STEP_3
} from 'constants/routes'
import { useHistory } from 'react-router-dom'
import React from 'react'
import { useTranslation } from 'react-i18next'
import { useSelector } from 'react-redux'

type Props = {
  step: 1 | 2 | 3 | 4 | 101
}

const Actions = ({ step }: Props) => {
  const { basket } = useSelector((store) => ({
    basket: store.basket
  }))
  const history = useHistory()
  const { t } = useTranslation()
  const isBasketEmpty = basket.length === 0

  const onClickPrevious = () => {
    switch (step) {
      case 1:
        history.push('/')
        break

      case 2:
        history.push(CHECKOUT_STEP_1)
        break

      case 3:
        history.push(CHECKOUT_STEP_2)
        break

      case 101:
        history.push(CHECKOUT_STEP_3)
        break

      default:
        history.push(CHECKOUT_STEP_3)
        break
    }
  }

  const isFirstPage = step === 1

  const onClickNext = () => {
    if (isFirstPage) {
      history.push(CHECKOUT_STEP_2)
    }
  }

  return (
    <Flexbox
      marginTop="2rem"
      alignItems="center"
      justifyContent="space-between"
    >
      <button
        className="button button-muted"
        onClick={onClickPrevious}
        type="button"
      >
        {isFirstPage && (
          <Box marginRight="0.5rem">
            <ShopOutlined />
          </Box>
        )}

        {t(
          isFirstPage
            ? 'checkoutPage.actions.backToShop'
            : 'checkoutPage.actions.back'
        )}
      </button>
      <button
        disabled={isBasketEmpty}
        className="button"
        onClick={onClickNext}
        type={isFirstPage ? 'button' : 'submit'}
      >
        <Box marginRight="0.5rem">
          {t(
            step === 4
              ? 'checkoutPage.actions.createOrder'
              : 'checkoutPage.actions.next'
          )}
        </Box>
        <ArrowRightOutlined />
      </button>
    </Flexbox>
  )
}

export default Actions
