/* eslint-disable react/jsx-one-expression-per-line */
import React from 'react'
import Flexbox from 'components2/Flexbox'
import styled from 'styled-components/macro'
import { useTranslation } from 'react-i18next'
import BasketToggle from './BasketToggle'
import useBasketClickToggle from './useBasketClickToggle'
import Box from 'components2/Box'

type Props = {
  basket: unknown[]
  onClearBasket: () => void
}

const Header = ({ basket, onClearBasket }: Props) => {
  const { t } = useTranslation()

  const onClickToggle = useBasketClickToggle()

  return (
    <Flexbox
      padding="m-size"
      width="100%"
      backgroundColor="white"
      // @ts-ignore
      zIndex="basket"
      alignItems="center"
    >
      <Box as="h3" flexGrow={1}>
        {t('common.basket.title')}

        {!!basket.length && (
          <span>
            {t(
              basket.length > 4
                ? 'common.basket.products_multiple'
                : basket.length > 1
                ? 'common.basket.products_plural'
                : 'common.basket.products',
              { count: basket.length }
            )}
          </span>
        )}
      </Box>

      {!!basket.length && (
        <Box marginRight="1rem">
          <button
            className="basket-clear button button-border button-border-gray button-small"
            disabled={basket.length === 0}
            onClick={onClearBasket}
            type="button"
          >
            <span>{t('common.basket.clear')}</span>
          </button>
        </Box>
      )}

      <span
        className="basket-toggle button button-border button-border-gray button-small"
        onClick={onClickToggle}
        role="presentation"
      >
        {t('common.basket.close')}
      </span>
    </Flexbox>
  )
}

export default Header
