/* eslint-disable implicit-arrow-linebreak */
import { CHECKOUT_STEP_3 } from 'constants/routes'
import { useDispatch, useSelector } from 'react-redux'
import { useHistory } from 'react-router-dom'
import { setContactDetailsLocaly } from 'redux/actions/new/contactDetailsActions'
import { setCustomerTypeLocaly } from 'redux/actions/new/customerTypeActions'
import { ContactDetails, CustomerType } from 'types'
import { object, string } from 'yup'

// eslint-disable-next-line no-undef
export type FormValues = ContactDetails

const useFormSubmit = () => {
  const { contactDetails } = useSelector((store) => ({
    contactDetails: store.NEWcontactDetails
  }))
  const dispatch = useDispatch()
  const history = useHistory()

  const onSubmitForm = (form: FormValues, customerType: CustomerType) => {
    dispatch(setCustomerTypeLocaly(customerType))
    dispatch(setContactDetailsLocaly(form))
    history.push(CHECKOUT_STEP_3)
  }

  const getInitialValues = (): FormValues => ({
    fullname: contactDetails.fullname ?? '',
    email: contactDetails.email ?? '',
    mobile: contactDetails.mobile || {
      country: '',
      countryCode: 'pl',
      dialCode: '48',
      value: ''
    },
    street: contactDetails.street ?? '',
    postCode: contactDetails.postCode ?? '',
    city: contactDetails.city ?? '',
    nip: contactDetails.nip ?? ''
  })

  const getSchema = (customerType: CustomerType) => {
    const base = object().shape({
      fullname: string()
        .required('To pole jest wymagane')
        .matches(
          /^(\w){3,} (\w){3,}$/,
          'To pole powinno zawiera imi i nazwisko'
        ),
      email: string()
        .email('Wprowadzony email jest niepoprawny')
        .required('To pole jest wymagane'),
      mobile: object()
        .shape({
          country: string(),
          countryCode: string(),
          dialCode: string(),
          value: string()
            .min(11, 'Numer telefonu powinien skada si z 9 cyfr')
            .required('Numer telefonu jest wymagany')
        })
        .required('Numer telefonu jest wymagany'),
      street: string().required('Adres jest wymagany'),
      postCode: string()
        .trim()
        .matches(
          /^(\d{2})-(\d{3})$/,
          'Kod pocztowy powinien mie format XX-XXX'
        )
        .required('To pole jest wymagane'),
      city: string().required('To pole jest wymagane')
    })

    const companySchema = base.shape({
      nip: string()
        .required('To pole jest wymagane')
        .length(10, 'NIP powinien mie 10 znakw')
        .matches(/^(\d{10})$/, 'NIP powinien zawiera tylko cyfry')
    })

    return customerType === 'INDIVIDUAL' ? base : companySchema
  }

  return {
    onSubmitForm,
    getInitialValues,
    getSchema
  }
}

export default useFormSubmit
