import { PlusOutlined } from '@ant-design/icons'
import React, { ReactNode } from 'react'
import Square from 'components2/Square'
import styled from 'styled-components/macro'
import { GridAreaProps } from 'styled-system'

type Props = {
  disabled?: boolean
  onClick: () => void
  children: ReactNode
} & GridAreaProps

const SimpleButton = ({ disabled, onClick, children, ...props }: Props) => {
  return (
    <Square {...props}>
      <button
        className="button button-border button-border-gray button-small basket-control basket-control-add"
        disabled={disabled}
        onClick={onClick}
        type="button"
      >
        {children}
      </button>
    </Square>
  )
}

export default SimpleButton
