/* eslint-disable no-unused-expressions */
import { CloseOutlined, MinusOutlined, PlusOutlined } from '@ant-design/icons'
import { Badge, ImageLoader } from 'components/common'
import Box from 'components2/Box'
import { displayMoney } from 'helpers/utils'
import PropType from 'prop-types'
import React from 'react'
import { useDispatch } from 'react-redux'
import {
  addQtyItem,
  minusQtyItem,
  removeFromBasket
} from 'redux/actions/basketActions'

import styled from 'styled-components'
import Square from 'components2/Square'
import { Link } from 'react-router-dom'
import Flexbox from 'components2/Flexbox'
import { BasketProduct } from 'types'
import SimpleButton from './SimpleButton'

type Props = {
  product: BasketProduct
}

const BasketItem = ({ product }: Props) => {
  const dispatch = useDispatch()

  const onAddQty = () => {
    dispatch(addQtyItem(product))
  }

  const onMinusQty = () => {
    if (product.quantity !== 1) {
      dispatch(minusQtyItem(product))
    }
  }
  const onRemoveFromBasket = () => dispatch(removeFromBasket(product))

  return (
    <Container>
      <SimpleButton
        disabled={product.quantity === 1}
        gridArea="button-minus"
        onClick={onMinusQty}
      >
        <MinusOutlined style={{ fontSize: '9px' }} />
      </SimpleButton>

      <SimpleButton gridArea="button-plus" onClick={onAddQty}>
        <PlusOutlined style={{ fontSize: '9px' }} />
      </SimpleButton>

      <Box width="2rem" height="2rem" gridArea="quantity">
        <Badge stanalone count={product.quantity} />
      </Box>

      <Box gridArea="picture" position="relative" width="9rem" height="9rem">
        <Square>
          <ImageLoader
            alt={product.name ?? ''}
            className="basket-item-img"
            src={product.gallery[0].url}
          />
        </Square>
      </Box>

      <Flexbox flexDirection="column" gridArea="name">
        <Link
          to={`/product/${product.id}`}
          onClick={() => document.body.classList.remove('is-basket-open')}
        >
          {product.name}
        </Link>

        {product.variant && (
          <Box as="span" marginTop="0.5rem" className="spec-title">
            {product.variant}
          </Box>
        )}
      </Flexbox>

      <Flexbox alignItems="center">
        <h4 className="my-0">{displayMoney(product.price)}</h4>

        <Box marginLeft="0.5rem">
          <span className="spec-title">{`(x ${product.quantity})`}</span>
        </Box>
      </Flexbox>

      <SimpleButton gridArea="button-remove" onClick={onRemoveFromBasket}>
        <CloseOutlined />
      </SimpleButton>
    </Container>
  )
}

const Container = styled.div`
  height: 100px;
  display: flex;
  align-items: center;
  /* @include bezier-transition(); */
  /* animation: slide-up 0.5s ease; */

  display: grid;
  grid-template-columns: 3.5rem 2rem 9rem auto 3.5rem;
  grid-gap: 0.5rem 2rem;
  grid-template-areas:
    'button-plus quantity picture name button-remove'
    'button-minus quantity picture alba button-remove';
`

BasketItem.propTypes = {
  product: PropType.shape({
    id: PropType.string,
    name: PropType.string,
    brand: PropType.string,
    price: PropType.number,
    quantity: PropType.number,
    description: PropType.string,
    keywords: PropType.arrayOf(PropType.string),
    selectedSize: PropType.string,
    selectedColor: PropType.string,
    imageCollection: PropType.arrayOf(PropType.string),
    sizes: PropType.arrayOf(PropType.number),
    image: PropType.string,
    imageUrl: PropType.string,
    isFeatured: PropType.bool,
    isRecommended: PropType.bool,
    availableColors: PropType.arrayOf(PropType.string)
  }).isRequired
}

export default BasketItem
