import { useTranslation } from 'react-i18next'
import { displayMoney } from 'helpers/utils'
import Select from 'react-select'
import { useBasket } from 'hooks'
import React, { useState } from 'react'
import { ProductBase } from 'types'

type Props = {
  product: ProductBase
}

const Form = ({ product }: Props) => {
  const { t } = useTranslation()
  const [variant, setVariant] = useState<string | null>(null)
  const { addToBasket, isItemOnBasket } = useBasket()

  const onSelectedSizeChange = (newValue: { label: string; value: string }) => {
    setVariant(newValue.value)
  }

  const handleAddToBasket = () => {
    addToBasket({
      ...product,
      quantity: 1,
      variant
    })
  }

  const isInBasket = isItemOnBasket(product.id, variant)

  return (
    <>
      {product.variants.length !== 0 && (
        <>
          <br />
          <div>
            <span className="text-subtle">{t('productPage.optionLabel')}</span>
            <br />
            <br />

            <Select
              placeholder="--Select Size--"
              onChange={onSelectedSizeChange}
              options={product.variants
                .sort((a, b) => (a < b ? -1 : 1))
                .map((option) => ({ label: option, value: option }))}
              styles={{
                menu: (provided: any) => ({ ...provided, zIndex: 10 })
              }}
            />
          </div>
        </>
      )}

      <h1>{displayMoney(product.price)}</h1>
      <div className="product-modal-action">
        <button
          className={`button button-small ${
            isInBasket ? 'button-border button-border-gray' : ''
          }`}
          onClick={handleAddToBasket}
          type="button"
        >
          {isInBasket
            ? t('productPage.removeFromBasket')
            : t('productPage.addToBasket')}
        </button>
      </div>
    </>
  )
}
export default Form
