import { ImageLoader } from 'components/common'
import React from 'react'
import Skeleton, { SkeletonTheme } from 'react-loading-skeleton'
import { useHistory } from 'react-router-dom'
import styled from 'styled-components'
import { displayMoney } from 'helpers/utils'
import { ProductBase } from 'types'

type Props = {
  product: ProductBase
}

const ProductTile = ({ product }: Props) => {
  const history = useHistory()

  const onClickItem = () => {
    if (!product) return
    history.push(`/product/${product.id}`)
  }

  return (
    <SkeletonTheme color="#e1e1e1" highlightColor="#f2f2f2">
      <Container onClick={onClickItem} role="presentation">
        <ImageWrapper>
          {product.gallery ? (
            <ImageLoader
              alt=""
              className="product-card-img"
              src={product.gallery[0].url}
            />
          ) : (
            <Skeleton width="100%" height="100%" />
          )}
        </ImageWrapper>

        <InfoWrapper>
          <h2>{product.name || <Skeleton width={80} />}</h2>
          <p className="text-subtle text-italic">
            {product.price ? (
              displayMoney(product.price)
            ) : (
              <Skeleton width={40} />
            )}
          </p>
        </InfoWrapper>
      </Container>
    </SkeletonTheme>
  )
}

const Container = styled.div`
  width: 100%;
  max-height: 30rem;
  border: 1px solid ${(props) => props.theme.colors['border-color']};

  &:hover {
    cursor: pointer;

    & > * img {
      transform: scale(1.1);
    }
  }
`

const ImageWrapper = styled.div`
  width: 100%;
  height: 20rem;
  background: #f1f1f1;
  position: relative;
  overflow: hidden;
  padding: 1rem;

  img {
    width: 100%;
    height: 100%;
    object-fit: contain;
    /* @include bezier-transition; */
  }
`

const InfoWrapper = styled.div`
  padding: 1.5rem;

  h2 {
    margin: 0;
  }

  p {
    margin-top: 0;
    margin-bottom: 0;
  }
`

export default ProductTile
