import { SET_CUSTOMER_TYPE_LOCALY } from 'constants/constants'
import { SetCustomerTypeLocalyPayload } from 'redux/actions/new/customerTypeActions'

type Props = {
  type: typeof SET_CUSTOMER_TYPE_LOCALY
  payload: SetCustomerTypeLocalyPayload
}

const customerTypeReducer = (state = null, action: Props) => {
  switch (action.type) {
    case SET_CUSTOMER_TYPE_LOCALY:
      return action.payload

    default:
      return state
  }
}

export default customerTypeReducer
