/* eslint-disable no-undef */
import { CHECKOUT_STEP_4 } from 'constants/routes'
import { useDispatch, useSelector } from 'react-redux'
import { useHistory } from 'react-router-dom'
import { setShippingLocaly } from 'redux/actions/new/shippingActions'
import { Shipping } from 'types'
import { object, string } from 'yup'

export const formSchema = object().shape({
  street: string().required('To pole jest wymagane.'),
  houseNr: string().required('To pole jest wymagane.'),
  postCode: string().required('To pole jest wymagane.'),
  city: string().required('To pole jest wymagane.')
})

export type FormValues = Shipping

const useFormSubmit = () => {
  const { shipping } = useSelector((store) => ({
    shipping: store.NEWshipping
  }))
  const dispatch = useDispatch()
  const history = useHistory()

  const onSubmitForm = (form: FormValues) => {
    dispatch(setShippingLocaly(form))
    history.push(CHECKOUT_STEP_4)
  }

  const getInitialValues = (): FormValues =>
    shipping || {
      street: '',
      houseNr: '',
      postCode: '',
      city: ''
    }

  return {
    onSubmitForm,
    getInitialValues
  }
}

export default useFormSubmit
