import {
  IS_AUTHENTICATING,
  LOADING,
  SET_AUTH_STATUS,
  SET_REQUEST_STATUS
} from 'constants/constants'
import {
  SetAuthenticatingPayload,
  SetAuthStatusPayload
} from 'redux/actions/miscActions'

const initState = {
  loading: false,
  isAuthenticating: false,
  authStatus: null,
  requestStatus: null,
  theme: 'light'
}

type Props =
  | {
      type: typeof SET_AUTH_STATUS
      payload: SetAuthStatusPayload
    }
  | {
      type: typeof IS_AUTHENTICATING
      payload: SetAuthenticatingPayload
    }

export default (state = initState, action: Props) => {
  switch (action.type) {
    case LOADING:
      return {
        ...state,
        loading: action.payload
      }
    case IS_AUTHENTICATING:
      return {
        ...state,
        isAuthenticating: action.payload
      }
    case SET_REQUEST_STATUS:
      return {
        ...state,
        requestStatus: action.payload
      }
    case SET_AUTH_STATUS:
      return {
        ...state,
        authStatus: action.payload
      }
    default:
      return state
  }
}
