import Button from 'components2/Button'
import Flexbox from 'components2/Flexbox'
import { ACCOUNT, SHOP } from 'constants/routes'
import React from 'react'
import { useTranslation } from 'react-i18next'
import { useHistory } from 'react-router-dom'
import { CheckCircleOutlined } from '@ant-design/icons'
import { useSelector } from 'react-redux'
import Checkout from '../commons/Checkout'
import Wrapper from '../commons/Wrapper'

const Result = () => {
  const { orders } = useSelector((store) => ({
    orders: store.orders
  }))

  const { t } = useTranslation()
  const history = useHistory()

  if (!orders.length) return null

  return (
    <Checkout>
      <Wrapper>
        <Flexbox justifyContent="center">
          <h1>{t('checkoutPage.result.title')}</h1>
        </Flexbox>

        <Flexbox justifyContent="center">
          <CheckCircleOutlined style={{ fontSize: '8rem' }} />
        </Flexbox>

        <Flexbox
          alignItems="center"
          flexDirection="column"
          justifyContent="center"
        >
          <p style={{ marginBottom: 0 }}>{t('checkoutPage.result.orderId')}</p>
          <h3>{orders[0].id}</h3>
        </Flexbox>

        <Flexbox justifyContent="space-between">
          <Button variant="secondary" onClick={() => history.push(SHOP)}>
            {t('checkoutPage.result.actions.backToShop')}
          </Button>

          <Button marginLeft="1rem" onClick={() => history.push(ACCOUNT)}>
            {t('checkoutPage.result.actions.confirmation')}
          </Button>
        </Flexbox>
      </Wrapper>
    </Checkout>
  )
}

export default Result
